import { addSeconds, format } from "date-fns";
import { useEffect, useState } from "react";
import Modal from "../modal";
import PropTypes from "prop-types";
import * as Constants from "../../constants/Constants";

const AddReminder = ({ selectedDateForReminder, handleClose, isReminder, reminderName, reminderTime, onHandleSubmit }) => {
    const [title, setTitle] = useState("");
    const [showError, setShowError] = useState(false);
    const [isEditReminder, setIsEditReminder] = useState(false);
    const [selectedDate, setselectedDate] = useState(format(new Date(), Constants.DATE_FORMAT))
    const [selectedTime, setSelectedTime] = useState(format(selectedDateForReminder, "HH:mm"));

    const handleChange = e => {
        setselectedDate(e.target.value);
    }
    const getTotalSeconds = selectedTime => {
        const time = selectedTime.split(":");
        const hr = time[0];
        const min = time[1];
        const totalSec = (hr * 3600) + (min * 60);
        return totalSec;
    }

    useEffect(() => {
        if (reminderName) {
            setIsEditReminder(true);
        }
        setTitle(reminderName)
    }, [reminderName])

    useEffect(() => {
        setSelectedTime(reminderTime)
    }, [reminderTime])

    useEffect(() => {
        setselectedDate(format(selectedDateForReminder, Constants.DATE_FORMAT));
    }, [selectedDateForReminder])

    const isValidTime = selectedTime => {
        const dateArr = selectedTime.split(":");
        if (dateArr[0] >= 24 || dateArr[1] >= 60) {
            return true
        }
    }

    const getValidatedData = () => {
        const utcDate = new Date(selectedDate);
        const isTimeValid = isValidTime(selectedTime);
        const totalSeconds = getTotalSeconds(selectedTime);
        const finalDate = addSeconds(utcDate, totalSeconds);
        return { utcDate, isTimeValid, finalDate };
    }

    const handleSubmit = () => {
        const { utcDate, isTimeValid, finalDate } = getValidatedData()
        if (selectedDate && selectedTime && title && utcDate.toString() !== Constants.INVALID_DATE && !isTimeValid && finalDate.toString() !== Constants.INVALID_DATE) {
            const payload = {
                id: finalDate.toString(),
                title: title
            };
            onHandleSubmit(isEditReminder, payload, false);
            setShowError(false);
            handleClose();
        } else {
            setShowError(true);
        }
    }

    const handleDelete = () => {
        const { finalDate } = getValidatedData();
        onHandleSubmit(isEditReminder, finalDate, true);
        handleClose();
    }

    const handleReminderName = e => {
        setTitle(e.target.value);
    }

    const handleSelectedTime = e => {
        setSelectedTime(e.target.value.toUpperCase())
    }
    return isReminder && <Modal>
        <strong>{isEditReminder ? "Edit Reminder" : "Add Reminder"}</strong>
        <div className="form-group">
            <label className="reminder-form-label">Title:</label>
            <input
                type="text"
                maxLength={30}
                required={true}
                value={title}
                name="modalInputName"
                onChange={handleReminderName}
                className="form-control"
            />
            <label className="reminder-form-label">Selected Date: (mm/dd/yyyy)</label>
            <input
                type="text"
                value={selectedDate}
                name="modalInputName"
                onChange={handleChange}
                className="form-control"
            />
            <label className="reminder-form-label">Selected time: (24-hr)</label>
            <input
                type="text"
                value={selectedTime}
                name="modalInputName"
                onChange={handleSelectedTime}
                className="form-control"
            />
        </div>
        <div className="form-group">
            {showError && <strong className="error">{Constants.REMINDER_ERROR_MESSAGE}</strong>}
            <button onClick={handleSubmit} type="button">
                {Constants.CONFIRM}
            </button>
            {isEditReminder && <button onClick={handleDelete} type="button">
                {Constants.DELETE}
            </button>}
            <button onClick={handleClose} type="button">
                {Constants.CANCEL}
            </button>
        </div>
    </Modal>
}

const propTypes = {
    reminderName: PropTypes.string,
    handleClose: PropTypes.func,
    isReminder: PropTypes.bool,
    selectedDateForReminder: PropTypes.instanceOf(Date),
    reminderTime: PropTypes.string,
    onHandleSubmit: PropTypes.func
}

AddReminder.prototype = propTypes;

export default AddReminder;