import { differenceInDays, endOfMonth, startOfMonth, setDate, format, compareAsc } from "date-fns";
import PropTypes from "prop-types";
import { useEffect, useState } from "react";
import { useQuery, useMutation, useQueryClient } from "react-query";
import * as Constants from "../../constants/Constants";
import { getAllReminders } from "../../service/helper";
import Cell from "../Cell";
import AddReminder from "../reminder/ReminderForm";
import CalendarNavigator from "./CalendarNavigator";
import { addNewReminder, deleteReminder, updateReminder } from "../../service/helper";

const Calendar = ({ value = new Date(), onChange }) => {
    const [isReminder, setIsReminder] = useState(false);
    const [selectedDate, setSelectedDate] = useState(value);
    const [remindersList, setRemindersList] = useState([]);
    const [reminderName, setReminderName] = useState("");
    const [reminderTime, setReminderTime] = useState(format(value, "HH:mm"));
    const startDate = startOfMonth(value);
    const endDate = endOfMonth(value);
    const numDays = differenceInDays(endDate, startDate) + 1;
    const prefixDays = startDate.getDay();
    const suffixDays = 6 - endDate.getDay();
    const displayReminderLimit = 4;
    const queryClient = useQueryClient();//new QueryClient();

    const { isLoading, data: list } = useQuery({
        queryKey: ["reminders"],
        queryFn: getAllReminders
    })

    const addNewReminderMutation = useMutation({
        mutationFn: addNewReminder,
        onSuccess: () => {
            queryClient.invalidateQueries({ queryKey: ['reminders'] })
        },
    })

    const { mutate: deleteReminderMutate } = useMutation(deleteReminder, {
        onSuccess: () => {
            queryClient.invalidateQueries({ queryKey: ['reminders'] });
        }
    })

    const { mutate: updateMutate } = useMutation(updateReminder, {
        onSuccess: () => {
            queryClient.invalidateQueries({ queryKey: ['reminders'] });
        },
    })

    useEffect(() => {
        if (list) {
            setRemindersList(list)
        }
    }, [list])

    const addReminder = (data) => {
        const date = setDate(value, data);
        setSelectedDate(date);
        setIsReminder(true);
    }

    const handleClose = () => {
        setReminderName("");
        setReminderTime(format(value, "HH:mm"));
        setIsReminder(false);
    }

    const openReminderList = (val, data) => {
        setReminderName(val.title);
        const time = format(new Date(val.id), "HH:mm")
        setReminderTime(time)
        addReminder(data);
    }

    const onHandleSubmit = (isEditReminder, payload, isDelete) => {
        if (isDelete) {
            deleteReminderMutate(payload);
        } else if (isEditReminder) {
            const isReminderAvailable = remindersList.filter(availableReminder => availableReminder.id === payload.id);
            if (isReminderAvailable.length) {
                updateMutate(payload)
            } else {
                addNewReminderMutation.mutate(payload);
            }
        } else {
            addNewReminderMutation.mutate(payload);
        }
    }

    return <div>
        {isLoading ? <strong>Loading . . .</strong> :
            <div className="calendar_month">
                <CalendarNavigator onChange={onChange} value={value} />
                {Constants.DAY_OF_WEEK.map((week, index) => (
                    <Cell key={index} className="week-days">{week.toUpperCase()}</Cell>
                ))}
                {Array.from({ length: prefixDays }).map((_, index) => (
                    <Cell className="other" key={index} />
                ))}
                {Array.from({ length: numDays }).map((_, index) => {
                    const date = index + 1;
                    let formattedDate = setDate(value, date);
                    const isCurrentDate = date === value.getDate();
                    let reminderList = remindersList !== undefined && remindersList.filter(reminder => reminder.id.includes(formattedDate.toDateString()));
                    reminderList = reminderList !== undefined && reminderList.sort((a, b) => compareAsc(new Date(a.id), new Date(b.id)))
                    return (
                        <Cell
                            key={date}
                            isActive={isCurrentDate}
                            isReminderAvailable={false}
                            onClick={() => addReminder(date)}
                            className="dates other"
                        >
                            {date}
                            <div style={{ display: "flex", flexDirection: "column", cursor: "pointer" }}>
                                {reminderList !== undefined && reminderList.map((reminder, index) => {
                                    return reminder.id.includes(formattedDate.toDateString()) && displayReminderLimit >= index && <div onClick={() => openReminderList(reminder, date)} key={reminder.id} style={{ display: "flex", flexDirection: "row", gap: 3 }}><span className="reminder-dot"></span> <span style={{ fontFamily: "serif", fontSize: 14 }}>{reminder.title}</span></div>
                                })}
                            </div>
                        </Cell>
                    );
                })}
                {Array.from({ length: suffixDays }).map((_, index) => (
                    <Cell className="other" key={index} />
                ))}
            </div>
        }
        {isReminder && <AddReminder onHandleSubmit={onHandleSubmit} reminderTime={reminderTime} reminderName={reminderName} selectedDateForReminder={selectedDate} isReminder={isReminder} handleClose={handleClose} />}
    </div>
}

const propTypes = {
    onChange: PropTypes.func,
    value: PropTypes.instanceOf(Date)
}

Calendar.prototype = propTypes;

export default Calendar;